 .home-wrapper{
    height: fit-content;
    /* background:linear-gradient(180deg, #CFFF0F 12.58%, #5AC727 100%); */
    background: #F771A6;
    padding-top: 0px;
    min-height:100vh;
    display: flex;
 }
 .story-wrapper{
    height: auto;
    /* background:linear-gradient(180deg, #CFFF0F 12.58%, #5AC727 100%); */
    background: #F771A6;
    min-height:100vh;
    /* display: flex; */
    padding:0px 40px;
 }

 .text-wrapper{
     min-width:50%;
     height:100vh;
     display: flex;
     padding:0px 40px 0px 40px;
     align-items: center;
     flex-direction: column;
     justify-content:center;
    /* padding-top: 80px; */
     /* background-color: ; */
 }
 .pic-wrapper{
    min-width:50%;
    height:auto;
    display: flex;  
    align-items: center;  
    justify-content: center; 
 }


 @media only screen and (max-width: 800px) {
    .home-wrapper{
        height: auto;
        /* background:#7BD6FF; */
        padding-top: 0px;
        min-height:100vh;
        display: block;
     }
     .text-wrapper{

        padding: 30px 20px 0px 20px;
     }
     .pic-wrapper{
      min-width:50%;
      height:auto;
      display: flex;  
      align-items: center;  
      justify-content: center; 
      padding:30px 0px;
   }
     .secondary-text-wrapper{
      min-height: auto;
      height: auto;
      padding-top:0px;
      padding-bottom: 40px;
     }
  }

@media only screen and (max-width: 400px) {
  .text-wrapper{
      padding: 20px 20px 0px 20px;
      height: fit-content;
   }
   .story-wrapper{
      height: auto;
      /* background:linear-gradient(180deg, #CFFF0F 12.58%, #5AC727 100%); */
      background: #F771A6;
      min-height:100vh;
      /* display: flex; */
      padding:0px 20px;
   }
}